@import "styles/base/_base.scss";
@import "styles/mixins/_mixins.scss";

.title-container {
  color: white;
  padding-top: 25vh;
  height: 700px;
  position: relative;
  z-index: 999;

  h2.title {
    color: white;
    margin-bottom: 20px;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 4.8em;
    line-height: 1.15em;
    font-weight: 700;
  }

  h4 {
    color: white;
    margin-bottom: 60px;
    font-family: "Roboto Slab", "Times New Roman", serif;
    font-size: 1.3em;
    line-height: 1.4em;
  }
}

.instagram {
  position: absolute;
  width: 100%;
  z-index: 2;

  .instagram-overlay {
    background: rgba(0, 0, 0, 0.6);
  }

  .instagram-pic {
    position: relative;
    width: 16.666666%;
    height: 350px;
    object-fit: cover;
    z-index: -1;
  }
}

.buttons a,
.buttons button {
  @include no-border-white;
  padding: 17px 35px;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 400;
  background-color: $primary-brand-color;
  margin-left: 5px;
  margin-right: 5px;
}

.buttons a:hover,
.buttons a:active,
.buttons button:hover,
.buttons button:active {
  @include no-border-white;
  background-color: $primary-brand-hovor-color !important;
}

.buttons a:unpublished:visited,
.buttons a:focus,
.buttons a:visited,
.buttons a:target .buttons button:unpublished:visited,
.buttons button:focus,
.buttons button:visited,
.buttons button:target {
  @include no-border-white;
  background-color: $primary-brand-color !important;
}

.home-container {
  margin-top: 2em;
  margin-bottom: 2em;
}

.info-container {
  margin-top: 2em;
  margin-bottom: 2em;

  p {
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 20px;
    line-height: 2;
  }

  p.support-text {
    line-height: 1.25;
  }

  h1.header-title {
    margin-bottom: 1em;
  }
}

.description-container {
  padding-right: 15%;
  padding-left: 15%;
}

div.mailchimp {
  width: 25%;
}

@media only screen and (max-width: $breakpoint-mobile) {
  div.mailchimp {
    width: 100%;
  }

  .instagram-pic {
    width: 50%;
    height: 233px;
  }

  .title-container h2.title {
    font-size: 4.25em;
  }
}
